.Flow {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  text-align: center;
  padding: 3em 2em;
  gap: 2em;
}

.Input {
  text-align: left;
  width: 100%;
  padding: 2em 1.125em 1em 1.125em;
  border: 2px solid #d9d9d9;
  border-radius: 0.5em;
}

.Sticky {
  position: fixed;
  width: 90%;
  padding: 1em 1em 1em 1em;
  bottom: 0;
}
